let Interface = {
  contentType: "application/json",

  /*************/
  dologin: { 
    url: "/manager/login/dologin",
    type: "POST",
    params: {
      "loginName": 'loginName',
      "password": "password" 
    }
  },

  /*************/
  logout: { 
    url: "/manager/login/logout",
    type: "POST"
  },

  /*************/
  getOperateResources: { 
    url: "/manager/login/getOperateResources",
    type: "POST"
  }
  
}

export default Interface;




