import React from 'react';
import {
  HashRouter as Router,
  Route,
  Link,
  Switch,
  Redirect
} from 'react-router-dom';
import _ from 'lodash';
import com from '../components/index';
import Home from '../components/home/home.jsx';
import NotFound from '../components/404/404.jsx';
import menuConfig from '../lib/menu.js';

//
let checkAuth = (pathname) => {
  let menu,
      roles,
      auth;

  menuConfig.map(function (doc) {
    doc.children.map( function (subItem) {
      console.log('subItem.path', subItem.path);
      console.log('pathname', pathname);
      if (pathname.indexOf(subItem.path) > -1) {
        menu = subItem;
      }
    })
  })

  if (menu) {
    roles = JSON.parse(sessionStorage.getItem("roles"));
    auth = (roles || []).indexOf(menu.role) > -1 ? true : false;
  } else {
    auth = false;
  }
  return auth;
}

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={props => {
    return checkAuth(props.location.pathname) ? (
      <Component {...props}/>
    ) : (
      <Redirect to={{
        pathname: '/login',
        state: { from: props.location }
      }}/>
    )
  }}/>
)

export default class ClRouter extends React.Component {
  render() {
    return (
        <div className="content">
          <Switch>
            <PrivateRoute exact  path="/" component={Home}/>
            {
              _.map(com, (component, index)=> {
                return (
                  <PrivateRoute  key={component.name} path={`/uplending/${index.toLocaleLowerCase()}`} component={component}/>
                )
              })
            }
          <PrivateRoute path="*" component={NotFound}/> 
          </Switch>
        </div>
    );
  }
}


